<template>
  <div class="addBank">
    <j-header title=""></j-header>
    <main>
      <ul>
        <li>
          <p></p>
          <jun-input v-model="form.phone"
            placeholder=""
            clear>
          </jun-input>
        </li>
        <li>
          <p></p>
          <jun-input v-model="form.account"
            placeholder=""
            clear>
          </jun-input>
        </li>
        <li>
          <p></p>
          <jun-input v-model="form.bank_card"
            placeholder=""
            clear>
          </jun-input>
        </li>
        <li>
          <p></p>
          <jun-input v-model="form.bank_branch"
            placeholder=""
            clear>
          </jun-input>
        </li>
        <li @click="showBank = true">
          <p></p>
          <jun-input v-model="bankName"
            placeholder=""
            disabled
            clear>
          </jun-input>
        </li>
        <li @click="showCity = true">
          <p></p>
          <jun-input v-model="city"
            placeholder=""
            disabled
            clear>
          </jun-input>
        </li>
        <li>
          <p></p>
          <jun-input v-model="imgCode"
            placeholder=""
            clear>
          </jun-input>
          <img-code type='0'/>
        </li>
        <li>
          <p></p>
          <jun-input v-model="form.smscode"
            placeholder=""
            clear>
          </jun-input>
          <send-code :data='{ type: 0, phone: form.phone, code: imgCode }'/>
        </li>
      </ul>
      <button @click="confirm"></button>
    </main>
    <j-city v-model="showCity" @city="cityInfo" title=""/>
    <j-bank v-model="showBank" @getBank="getBank"/>
  </div>
</template>

<script>
// @ is an alias to /src
import JHeader from '@/components/JHeader'
import JunInput from '@/components/JunInput'
import imgCode from '@/components/imgCode'
import sendCode from '@/components/sendCode'
import JCity from '@/components/JCity'
import JBank from '@/components/JBank'

export default {
  name: 'addBank',
  components: {
    JHeader,
    JunInput,
    imgCode,
    sendCode,
    JCity,
    JBank
  },
  data () {
    return {
      showCity: false,
      showBank: false,
      imgCode: '',
      city: '',
      bankName: '',
      form: {
        phone: '',
        account: '',
        bank_card: '',
        bank_branch: '',
        bank_code: '',
        province: '',
        city: '',
        area: '',
        smscode: '',
        type: 10
      },
      validate: {
        account: '',
        bank_code: '',
        bank_branch: '',
        bank_card: '',
        phone: '',
        smscode: '',
        province: '',
        city: '',
        area: ''
      }
    }
  },
  created () {
  },
  methods: {
    cityInfo (obj) {
      var str = ''
      for (var key in obj) {
        if (obj[key]) {
          str += obj[key] + '/'
          this.form[key] = obj[key]
        }
      }
      this.city = str.substring(0, str.length - 1)
    },
    getBank (obj) {
      this.bankName = obj.name
      this.form.bank_code = obj.code
    },
    async verify () {
      for (var key in this.form) {
        if (key !== 'type') {
          if (!this.form[key]) {
            this.$toast({ message: this.validate[key], position: 'top' })
            return false
          }
        }
      }
    },
    async confirm () {
      const condition = await this.verify()
      condition !== false && this.request({
        url: '/bank',
        method: 'post',
        data: this.form
      }).then(res => {
        this.$toast({
          message: res.message,
          position: 'top'
        })
        res.code === 1 && this.$router.go(-1)
      })
    }
  },
  mounted () {
  }
}
</script>

<style lang="scss" scoped>
  @import './index.scss';
</style>
